@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg: #242526;
  --bg-accent: #484a4d;
  --text-color: #dadce1;
  --border: 1px solid #474a4d;
  --border-radius: 8px;
  --speed:500ms;
}

a{
  color: #dadce1;
  color: var(--text-color);
  text-decoration: none;
}

button{
  color: #dadce1;
  color: var(--text-color);
}
body {
  background-color: #222121;
}


.App {
  text-align: center;
  height:100vh;
  width:auto;
  padding-top: 15px;
  padding-bottom: 50px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #222121;
  white-space: nowrap;
  overflow: auto;
  display: flex;
}


.App div::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

.App div::-webkit-scrollbar{
	width: 12px;
	background-color: #F5F5F5;
}

.App div::-webkit-scrollbar-thumb {
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(58, 48, 48, 0.3);
	background-color: rgb(160, 160, 160);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.node {
  margin-top: -3px;
  margin-bottom: -2px;
  margin-right: -1px;
  border: 1px solid rgb(165, 165, 165);
  background-color: white;
  width: 25px;
  height: 25px;
  display: inline-block;
  z-index: 10;
}

.node-finish {
  background-color: rgb(255, 67, 67);
}

.node-start {
  background-color: rgb(39, 255, 111);
}

.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: .75s;
          animation-duration: .75s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  z-index: 1;
}

.node-visited-no-animation {
  background-color: rgb(50, 197, 219);
  border: 1px solid rgba(255, 255, 255, 0.438);
  z-index: 1;
}

@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
    background-color: rgb(38, 38, 107);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 1);
  }

  75% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 217, 159, 1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(50, 197, 219);
    border: 1px solid rgba(255, 255, 255, 0.438);
    z-index: 1;
}
}

@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
    background-color: rgb(38, 38, 107);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 1);
  }

  75% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 217, 159, 1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(50, 197, 219);
    border: 1px solid rgba(255, 255, 255, 0.438);
    z-index: 1;
}
}

.node-wall {
  background-color: rgb(51, 48, 48);
  border: 1px solid rgb(51, 48, 48);  
  -webkit-animation-name: wallAnimation;  
          animation-name: wallAnimation;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: .1s;
          animation-delay: .1s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes wallAnimation {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);

  }
}

@keyframes wallAnimation {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);

  }
}


.node-shortest-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.node-shortest-path-no-animation {
  background-color: rgba(255, 255, 106, 0.966);
  border: 1px solid rgb(233, 233, 60);

}

@-webkit-keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 146, 74);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 240, 96);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(255, 255, 106, 0.966);
    border: 1px solid rgb(233, 233, 60);
  }
}

@keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 146, 74);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 240, 96);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(255, 255, 106, 0.966);
    border: 1px solid rgb(233, 233, 60);
  }
}

.node-weight-2 {
  background-image: linear-gradient(
    45deg,
    rgba(0,0,0,0.5) 0%,
    transparent 0%,
    transparent 50%,
    rgba(0,0,0,0.5) 0%,
    rgba(0,0,0,0.5) 55%,
    transparent 0%,
    transparent 
  );
  background-size:5px 5px;
}

.node-weight-3 {
  background-image: linear-gradient(
    45deg,
    rgba(0,0,0,0.5) 25%,
    transparent 25%,
    transparent 50%,
    rgba(0,0,0,0.5) 50%,
    rgba(0,0,0,0.5) 75%,
    transparent 75%,
    transparent 
  );
  background-size:5px 5px;
}

.node-weight-4 {
  background-image: linear-gradient(
    45deg,
    rgba(0,0,0,0.5) 25%,
    transparent 25%,
    transparent 0%,
    rgba(0,0,0,0.5) 50%,
    rgba(0,0,0,0.5) 75%,
    transparent 75%,
    transparent 
  );
  background-size:5px 5px;
}

.dropdownButton::after {
  content: '▾';
  padding-left: 5px;
  font-size: 1.5em;
}

.dropdownButtonOpen {
}
.dropdownButtonOpen::after {
  content: '▾';
  padding-left: 5px;
  font-size: 1.5em;
  color: rgb(68, 155, 255);
}

.dropdownContent { 
  position: absolute;
  width: 170px;
  -webkit-transform: translateX(-45%);
          transform: translateX(-45%);
  background-color: var(--bg);
  border: var(--border);
  border-radius: var(--border-radius);
  padding: 1rem;
  overflow: hidden;
  z-index: 1;
  -webkit-transition: height var(--speed) ease;
  transition: height var(--speed) ease;
} 

.algorithms {
  top: 257px;
  left: 130px;
}

.blocks {
  top: 347px;
  left: 130px;
}

.dropdownContent a {
  height: 30px;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius);
  -webkit-transition: background var(--speed);
  transition: background var(--speed);
  padding: 0.5rem;
  cursor: pointer;
  /* cursor: pointer;
  font-size: 1em; 
  display: flex; 
  padding: 12px 16px;
  text-decoration: none;
  color: rgb(230, 230, 230); */
}
.dropdownContent a:hover {
  background: #525357;
}

.menu {
  width: 100%;
}
.grid {
  margin: 0px 3px 153px 110px;
  flex: 6 1;
  border: 3px #bdfbff solid;
  background-color: white;
  overflow: hidden;
}

.buttons {
  margin: 0px -70px 150px 0px;
  flex: 1 1;
  border: 2px #ccc solid;
  background-color: rgba(49, 44, 43, 0.6);
  display: flex;
  flex-direction: column;
  flex-basis: 0%;
  min-width: 200px;
  align-items: stretch;
  overflow: hidden;
}


.buttons button {
  align-self: center;
  margin: 20px 0 0 0;
  height: 40px;
  width: 200px;
  border-radius: 4px;
  position: relative;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
  font-size: 1.25em;
  font-weight: normal;
  margin: 25px;
  background-color: var(--bg-accent);
  border: var(--border);
  border-radius: var(--border-radius)
}

.buttons div {
  align-self: center;
}

.buttons button:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.buttons button:disabled {
  -webkit-filter: brightness(.6);
          filter: brightness(.6);
}

